import Link from 'next/link'

import {getAllPosts, PostMetadata} from '@/lib/mdx'
import DateComponent from '@/app/components/Date'
import Avatar from '@/app/components/Avatar'

const Post = ({post}: {post: PostMetadata}) => {
  const {title, slug, excerpt, date, author} = post

  return (
    <article
      key={slug}
      className="border border-gray-200 rounded-sm p-6 bg-gray-50 flex flex-col justify-between transition-colors hover:bg-white relative"
    >
      <Link className="hover:text-brand underline transition-colors" href={`/posts/${slug}`}>
        <span className="absolute inset-0 z-10" />
      </Link>
      <div>
        <h3 className="text-2xl font-bold mb-4 leading-tight">{title}</h3>

        <p className="line-clamp-3 text-sm leading-6 text-gray-600 max-w-[70ch]">{excerpt}</p>
      </div>
      <div className="flex items-center justify-between mt-6 pt-4 border-t border-gray-100">
        {author && author.firstName && author.lastName && (
          <div className="flex items-center">
            <Avatar person={author} small={true} />
          </div>
        )}
        <time className="text-gray-500 text-xs font-mono" dateTime={date}>
          <DateComponent dateString={date} />
        </time>
      </div>
    </article>
  )
}

const Posts = ({
  children,
  heading,
  subHeading,
}: {
  children: React.ReactNode
  heading?: string
  subHeading?: string
}) => (
  <div>
    {heading && (
      <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl lg:text-5xl">
        {heading}
      </h2>
    )}
    {subHeading && <p className="mt-2 text-lg leading-8 text-gray-600">{subHeading}</p>}
    <div className="pt-6 space-y-6">{children}</div>
  </div>
)

export const MorePosts = async ({skip, limit}: {skip: string; limit: number}) => {
  const allPosts = getAllPosts()
  const filteredPosts = allPosts.filter((post) => post.slug !== skip).slice(0, limit)

  if (filteredPosts.length === 0) {
    return null
  }

  return (
    <Posts heading={`Recent Posts (${filteredPosts.length})`}>
      {filteredPosts.map((post) => (
        <Post key={post.slug} post={post} />
      ))}
    </Posts>
  )
}

export const AllPosts = async () => {
  const allPosts = getAllPosts()

  if (allPosts.length === 0) {
    return (
      <div className="text-center py-12">
        <h2 className="text-2xl font-bold mb-4">No posts yet</h2>
        <p className="text-gray-600">
          Create your first post by adding a <code className="bg-gray-100 px-2 py-1 rounded">.mdx</code> file to{' '}
          <code className="bg-gray-100 px-2 py-1 rounded">content/posts/</code>
        </p>
      </div>
    )
  }

  return (
    <Posts
      heading="Recent Posts"
      subHeading={`${allPosts.length === 1 ? 'This blog post is' : `These ${allPosts.length} blog posts are`} from your MDX files.`}
    >
      {allPosts.map((post) => (
        <Post key={post.slug} post={post} />
      ))}
    </Posts>
  )
}
